import React from 'react';
import { InteractiveForceGraph, ForceGraphNode, ForceGraphLink } from 'react-vis-force';

import Nodes from './../data/testNodes';

export default class TestForce extends React.Component {
	
	render() {
		return (
			
			<InteractiveForceGraph
				strength={100}
				simulationOptions={{ height: 300, width: 300, animate: true }}
				labelAttr="label"
				onSelectNode={(node) => console.log(node)}
				highlightDependencies
			>
				{
					Nodes.nodes.map((d, idx) => {

						return (
							<ForceGraphNode node={{ id: idx, label: d.name }} fill={d.color} />

						);
					})
				}
			</InteractiveForceGraph>
			
    	);
    }
}


